.chatInterface {
  border-top: 1px solid #333;
  background-color: white;
  padding: 10px;
}
.chatInterface input {
  width: 50%;
  padding: 10px 15px;
}
.chatInterface button {
  cursor: pointer;
  padding: 10px 14px;
  border: 0;
  user-select: none;
  outline: none;
  color: white;
  background-color: #2ECC71;
  border-bottom-right-radius: 5px;
  border-top-right-radius: 5px;
}
