li {
  margin: 45px 0 0 0;
  font-weight: 300;
}
li .user {
  margin: -30px 0 0 0;
  color: #333;
}
li .user img {
  width: 65px;
  height: 65px;
  border-radius: 50%;
  background-color: #f3f3f3;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
}
li .date {
  font-size: 14px;
  color: #a6a6a6;
}
li .message {
  display: block;
  padding: 10px 10px 24px 10px;
  position: relative;
  color: #fff;
  font-size: 15px;
  background-color: #2ECC71;
  border-radius: 3px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
}
li .message:before {
  content: '';
  position: absolute;
  border-top: 16px solid rgba(0, 0, 0, 0.15);
  border-left: 16px solid transparent;
  border-right: 16px solid transparent;
}
li .message:after {
  content: '';
  position: absolute;
  top: 0;
  border-top: 17px solid #2ECC71;
  border-left: 17px solid transparent;
  border-right: 17px solid transparent;
}
li .message p {
  margin: 0;
  padding: 0;
  transition: all 0.1s;
}
li .message button {
  float: right;
}
li.other .user {
  float: right;
}
li.other .date {
  float: right;
  margin: -20px 10px 0 0;
}
li.other .message {
  margin: 0 90px 0 0;
}
li.other .message:before {
  margin: -9px -16px 0 0;
  right: 0;
}
li.other .message:after {
  content: '';
  right: 0;
  margin: 0 -15px 0 0;
}
li.you .user {
  float: left;
}
li.you .date {
  float: left;
  margin: -20px 0 0 10px;
}
li.you .message {
  margin: 0 0 0 90px;
}
li.you .message:before {
  margin: -9px 0 0 -16px;
  left: 0;
}
li.you .message:after {
  content: '';
  left: 0;
  margin: 0 0 0 -15px;
}
